.page {
  min-width: 320px;
  height: 100%;
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-weight: 400;
  background-color: #ffffff;
  scroll-behavior: smooth;
}

.page__body {
  min-height: 100%;
  margin: 0;
}

@media (max-width: 1023px) {
  .page__body--static {
    overflow: hidden;
  }
}

.page-header__nav-toggle {
  display: none;
}
@media (max-width: 1023px) {
  .page-header__nav-toggle {
    width: 50px;
    height: 50px;
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    background-color: #0ad9c6;
    border: none;
    outline: none;
    cursor: pointer;
  }
  .page-header__nav-toggle::before, .page-header__nav-toggle::after {
    width: 22px;
    height: 1px;
    content: "";
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: block;
    background-color: #000000;
  }
  .page-header__nav-toggle::before {
    top: 18px;
  }
  .page-header__nav-toggle::after {
    bottom: 17px;
  }
}
.page-header__nav-toggle--nojs {
  display: none;
}
@media (max-width: 1023px) {
  .page-header__nav-toggle--close {
    z-index: 4;
    background-color: transparent;
    background-image: url("../img/svg/close.svg");
    background-repeat: no-repeat;
    background-position: center center;
  }
  .page-header__nav-toggle--close::before, .page-header__nav-toggle--close::after {
    display: none;
  }
}

@media (max-width: 1023px) {
  .page-header__nav-toggle span {
    width: 22.5px;
    height: 1px;
    background-color: #000000;
    display: block;
  }
}

